<template>
    <div class="mc-bg pl-2 ml-1">
        <strong>聯絡人</strong>
        <div class="pl-2">
            <div class="mc-item">
                聯絡人：{{ member.contact_named }}
            </div>
            <div class="mc-item">
                電話：{{ member.phoned }}
            </div>
            <div class="mc-item">
                郵箱：{{ member.email }}
            </div>
        </div>
        <br/>
        <strong>付款</strong>
        <div class="pl-2">
            <div class="mc-item">
                期限：{{ member.pay_time.named }}
            </div>
            <div class="mc-item">
                方式：{{ show_payway(member.pay_way) }}
            </div>
        </div>

        <br/>
        <br/>

        <button class="btn btn-outline-info btn-sm btn-block">跳轉至修改界面</button>
    </div>
</template>

<script>
import view from '@/common/es/view'
import tool from '@/common/es/tool'
import conn from '@/service/conn'
    export default {
        name: '',
        props: {
            member: {
                type: Object
            }
        },
        data() {
            return {
                
            }
        },
        methods: {
            inList(id, list) {
                return tool.inList(id, list)
            },
            show_payway(v) { return view.ser_listing_paymet(v) },
            pay_status(v) { return view.ser_pay_status(v) },
            ser_timed(v) {return tool.timed.ser_timed(v, true, true) },
            ser_timed_now_month(v, id) { 
                const res = tool.timed.is_now_month(v)
                
                if (res) {
                    return `<div class="tip tip-success fw-b">!</div>`
                    // return `<span class="tip-now-month">本月</span>`
                } 
                return ''
            },
        }
    }
</script>

<style lang="sass" scoped>
@import "@/common/sass/base.sass"
@import "@/common/sass/ready.sass"
    
</style>


