<template>
    <panel-layout :topH="240">

        <div class="row h-full mc-hui d-block def-shadow" slot="layout_top">
            
            <div class="col-12 every-top">
                <div class="pl-2 w-full">
                    <member-msg-center-card :member="membery" v-if="membery != null"></member-msg-center-card>
                </div>
            </div>
            <hr/>
            <div class="col-12 every-bottom">
                <div class="pl-2 w-full">
                    <member-msg-right-card :member="membery" v-if="membery != null"></member-msg-right-card>
                </div>
            </div>
                
        </div>

        <panel-layout-panel slot="layout_center" :bottomH="37">
            <h5 slot="header_1">
                發票
            </h5>

            <div slot="content_1">
                <member-msg-center-listing 
                    :member="membery" v-if="membery != null"
                ></member-msg-center-listing>

                <div class="float-right">

                    <pager-option :_page="pageLink" @loadData_Father="null"></pager-option>
                </div>
            </div>
            
            <h5 slot="header_2">
                報價單
            </h5>
            <div slot="content_2">
                <member-msg-center-prices
                    :member="membery" v-if="membery != null"
                ></member-msg-center-prices>
                
                <div class="float-right">

                    <pager-option :_page="pageLink" @loadData_Father="null"></pager-option>
                </div>
            </div>

        </panel-layout-panel>

    </panel-layout>
</template>

<script>
import conn from '@/service/conn'
import PanelLayout from '@/components/Layout/PanelLayout.vue'
import MemberMsgCenterCard from './Card/MemberMsgCenterCard.vue'
import MemberMsgRightCard from './Card/MemberMsgRightCard.vue'
import MemberMsgCenterListing from './Body/MemberMsgCenterListing.vue'
import MemberMsgCenterPrices from './Body/MemberMsgCenterPrices.vue'
import PanelLayoutPanel from '../../../components/Layout/PanelLayoutPanel.vue'
import PagerOption from '../../../components/Filter/PagerOption.vue'
    export default {
  components: { PanelLayout, MemberMsgCenterCard, MemberMsgRightCard, MemberMsgCenterListing, MemberMsgCenterPrices       
,
    PanelLayoutPanel,
    PagerOption         },
        name: '',
        data() {
            return {
                membery: null,
                pageLink: {
                    next: '',
                    previous: '',
                    count: 0,
                    offsetNow: 0,
                    limit: 100
                }
            }
        },
        mounted() {
            this.loadMember()
        },
        methods: {
            async loadMember() {
                const res = await conn.member.getList_Member({}, true)

                if (conn.judge_response_list(res)) {
                    this.membery = res[0]
                    console.log(this.membery)
                }
            }
        },
    }
</script>

<style lang="sass" scoped>
@import "@/common/sass/base.sass"
@import "@/common/sass/ready.sass"
</style>


