<template>
    <div class="mc-bg pt-4 pb-4">
        <div class="mc-face float-left"></div>
        <div class="mc-msg float-left ml-3">
            <h4 class="pt-2">
                {{ member.named }}
            </h4>
            <div>
                编号：{{ member.num }}
            </div>
        </div>

        <div class="w-full pt-4">
            <strong>地址：</strong>{{ member.addr }}
        </div>
    </div>
</template>

<script>
    export default {
        name: '',
        props: {
            member: {
                type: Object
            }
        },
        data() {
            return {
                
            }
        }
    }
</script>

<style lang="sass" scoped>
@import "@/common/sass/base.sass"
@import "@/common/sass/ready.sass"
</style>


